


<script>
	let userObject=null;
	const userBase= window.userbase;
	
	let authPromise= userBase.init({appId: '4bef59b3-7749-417d-92aa-b69056492dab'}).then(({user}) => userObject = user)

	let username, password;
	
	var dateObj = new Date();
	var month = dateObj.getUTCMonth() + 1; //months from 1-12
	var day = dateObj.getUTCDate();
	var year = dateObj.getUTCFullYear();

	let newdate = day + "/" + month + "/" + year;
	

	const signIn = ()=> authPromise = userBase.signIn({username, password}).then(user => userObject = user);
	const signUp= () => authPromise= userBase.signUp({username, password}).then(user => userObject = user);
	const signOut = () => authPromise = userBase.signOut ().then (() => userObject = null);

	let postPromise;
	let posts = [], newPost, icerik;
	const databaseName ="posts";
	function changeHandler(items) {posts= items};
	$: if (userObject)  userBase.openDatabase({databaseName, changeHandler});


	function addPost (){
		userBase.insertItem({databaseName, item:{newPost,icerik, newdate} });
		newPost="";
		icerik="";
	}



	function deletePost(itemId) {
        postPromise = userbase.deleteItem({databaseName, itemId});
	}
	
</script>


<main>
	<h2>Kalk Yap</h2> {#if userObject}<button on:click={signOut} type="button" class="btn btn-secondary">Sign Out</button>{/if}
	<br>
	<h3>Yapman için her şeyi burada bulacaksın!</h3>
	{#await authPromise} loading...{:then _}
		{#if !userObject}
	<div class="signUpDiv">
	<form>
		<input id="username" bind:value="{username}" class="signUpInput" type="text" name="Ad Soyad" placeholder="Ad Soyad">
		<br>
		<input id="password" bind:value={password} class="signUpInput" type="password" name="sifre" placeholder="Sifre">
		<br>
		<button on:click={signUp} type="button" class="signUpBtn">Sign Up</button>
		<button on:click={signIn} type="button" class="signUpBtn">Sign In</button>

	</form>
	</div>
		{:else}
			<div class="card text-center yazi">
  				<div class="card-header">
  				  Hoş Geldin Sevgili {userObject.username} 
  				</div>
  				<div class="card-body">
    				<h5 class="card-title">Tarihe Bir Not Bırakma Zamanı</h5>
					
						
						
						
					
					<div style="margin-bottom:20px;" class="input-group">
					  <div class="input-group-prepend">
					    <span class="input-group-text">Başlık</span>
					  </div>
					  	
						<input  class="form-control" placeholder="Başlık" id="Baslik" type="text" bind:value={newPost}>
						
					</div>
					<div style="margin-bottom:20px;" class="input-group">
					  	<div class="input-group-prepend">
					  	  <span class="input-group-text">İçerik</span>
					  	</div>
					  	
						<textarea class="form-control"   id="icerik" bind:value={icerik} placeholder="İçerik"></textarea>
					</div>
    				
								
					
					<button style="width: -webkit-fill-available;" on:click={addPost} type="button" class="btn btn-primary">Not Ekle</button>
					
  				</div>
  				<div class="card-footer text-muted">
					
					<img src="https://img.icons8.com/fluent/48/000000/hand-with-pen.png" alt="write">
