31 8.0625,8.0625v66.08562c0,2.09356 -1.69581,3.78938 -3.78937,3.78938z" fill="url(#color-7_119436_gr7)"></path></g></g></svg>
					<img src="https://img.icons8.com/emoji/48/000000/waving-hand-emoji.png" alt="hello">
  				</div>
			</div>
			
			
			
			

			

		{/if}
	{:catch error}Error! {error} {/await}



	
</main>

<div class="container blog">
  <div class="row">
  {#each posts as post}
    <div class="col-md">
			<div class="card" >
  			<div class="card-body">
    			<h5 class="card-title">{post.item.newPost}</h5>
    			<h6 class="card-subtitle mb-2 text-muted">{post.item.newdate}</h6>
				<p class="card-text">{post.item.icerik}</p>
    			
				
				<button style="background: none; border: none; width: 40px; height: 40px;}" on:click={() => deletePost(post.itemId)}>
					<img style="width: 32px; height: 32px;" src="https://img.icons8.com/color/48/000000/delete-forever.png" alt="delete">
				</button>
				
    			
  			</div>
		</div>
    </div>
	{/each}
    
    
  </div>
  
</div>


<style>

	.card{
		width: auto;
		margin-bottom: 20px;
		-webkit-box-shadow: 0px 0px 30px -18px rgba(56,2,2,0.89);
    	-moz-box-shadow: 0px 0px 30px -18px rgba(56,2,2,0.89);
	    box-shadow: 0px 12px 30px -25px rgba(56,2,2,0.89);
		}
	.blog{
		margin-top: 50px;
	}

	
	
	.signUpDiv{
		width: 100%;
		display: inline-block;
	}
	.signUpInput{
		width: 100%;
	}
	.signUpBtn{
		width: 100%;
	}
	main {
		text-align: center;
		padding: 1em;
		margin: 0 auto;
	}

	

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
		.signUpDiv{
			width: 40%;
		}
		.yazi{
			max-width: 50%;
			margin: auto;
		}

	}
</style>